@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap');

* {
  margin: 0;
}

@sizead: 75px; // esto es la diferencia de la card + su min-width

body {
  font-family: 'Roboto', sans-serif;
}

.producto {
  display: flex;
  align-items: flex-start;
  // max-width: @size;
}

.marcas {
  position: relative;
  transform: translateX(-@sizead);
  display: flex;
  flex-direction: column;
  max-width: 40px;
  align-self: flex-start;
  justify-content: space-between;
  flex: 1;
  direction: rtl;

  .dr {
    display: flex;
    height: 20px;
    min-width: 20px;
    width: fit-content;
    background-color: transparent;
    margin-bottom: 20%;
  }
}

.imagen {
  display: flex;

  .foto {
    // position: absolute;
    // top: 0;
    // object-fit: cover;
    // width: @size;
    z-index: -100;
  }
}

.recuadro {
  position: relative;
  // transform: translateX(-@size);
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  // height: @size;
  background-color: transparent;
  margin-bottom: 6%;

  .rank {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    box-sizing: border-box;
    min-height: 20px;
    min-width: 20px;
    color: white;
    padding: 1em 0.5em 1em 0.5em;
    font-weight: 100;
    margin-bottom: 30%;

  }

  .rank p {
    margin: 0;
    align-self: center;
  }

  .label {
    display: block;
    background-color: rgb(255, 255, 255);
    width: fit-content;
    height: fit-content;
    min-width: 55px;
    min-height: 20px;

    p {
      margin: 0;
      text-transform: uppercase;
      vertical-align: middle;
      line-height: 20px;
      font-weight: 700;
      margin-left: 0.6em;
      margin-right: 0.5em;
      padding: 0.2em;
    }

  }
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(0, 0, 0);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}